    getBanner: function getBanner() {
      var that = this;
      var formdata = new URLSearchParams();
      formdata.append('id', this.room_id);
      that.axiosPost("/room/banner", formdata).then(function (res) {
        that.bannerList = res.data.banner;
      });
    },
